.movie {
    position: relative;
    background:#2d3436;
    height: 200vh;
  }
  *{
    padding: 0;
    margin: 0;
    box-sizing: border-box;

}
  .movie_detail {
    width: 100%;
  }
  .movie_detailLeft {
    width: 40%;
  }
  .movie_detailRight {
    width: 50%;
  }
  .movie_intro {
    width: 80%;
    margin: 0 auto;
  }
  .movie_backdrop {
    width: 100%;
    height: 750px;
  }
  .movie_posterBox {
    width: 300px;
    position: absolute;
    top: 30%;
    left: 15%;
  }
  .movie_poster {
    width: 100%;
  }
  .movie_detailRight {
    position: absolute;
    top: 30%;
    left: 35%;
    color: #fff;
  }
  .movie_genres {
    margin-top: 30px;
    padding: 10px 20px;
  }
  .movie_genre {
    margin-left: 10px;
    border: #fff solid 1px;
    border-radius: 25px;
    padding: 10px 20px;
  }
  .movie_detailRightBottom {
    margin-top: 30px;
    line-height: 30px;
  }
  .movie_name{
    font-size: 50px;
    font-family: 'Limelight', cursive;
  }